.call {
  &__link {
    font-family: "Open Sans", sans-serif;
    font-size: 24px;
    font-weight: 600;
    line-height: 36px;
    color: rgba(255, 255, 255, 1);
    text-decoration: none;
  }

  &__paragraph {
    font-family: "Open Sans", sans-serif;
    font-size: 24px;
    font-weight: 600;
    line-height: 36px;
    color: rgba(255, 255, 255, 1);

    @include hover (color, rgba(20, 78, 212, 1));
  }
}
